
@mixin reviews_style_01 {
	p {
		margin: 0;
	}
	.view {
		@extend %layout;
	}
	.view-content {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
	}
	.views-row {
		width: 32%;
		@media screen and (min-width: 480px) and (max-width: 979px) {
			width: 49%;  		
			&:last-child {
  			display: none; //hidden
  		}
  	}
  	@media screen and (min-width: 320px) and (max-width: 479px) {
  		width: 100%;
  		&:last-child {
  			display: none; //hidden
  		}
  	}
  	@media screen and (min-width: 320px) and (max-width: 479px) {
  		&:not(:last-child) {
  			margin-bottom: 50px;  			
  		}
  	}
  }
  .views-field-field-izobrazenie {
  	float: left;
  	margin: 0 20px 30px 0px;
  	@media screen and (min-width: 320px) and (max-width: 479px) {
  		margin-bottom: 15px;
  	}
  	.field-content {
  		overflow: hidden;
  		width: 100px;
  		height: 100px;
  		border-radius: 50%;
  		box-shadow: 2px 3px 7px 1px #999;
  		@media screen and (min-width: 320px) and (max-width: 767px) {
  			width: 70px;
  			height: 70px;
  		}
  		img {
  			width: auto;
  			height: auto;
  			max-width: 100%;
  			max-height: 100%;
  			display: block;
  		}
  	}
  }
  .views-field-title {
  	margin-bottom: 10px;
  	.field-content {
  		display: block;
  		font-weight: 700;
  		font-size: 20px;
  		font-family: $font_title;
  		line-height: 1.2;
  		@media screen and (min-width: 480px) and (max-width: 767px) {
  			font-size: 16px;
  		}
  	}
  }
  .views-field-field-dolznost {
  	.field-content {
  		font-size: 14px;
  		line-height: 1.2;
  		color: #444;
  		text-transform: lowercase;
  		@media screen and (min-width: 320px) and (max-width: 767px) {
  			font-size: 12px;
  		}
  	}
  }
  .views-field-body {
  	clear: both;
  	background-color: $color_bg;
  	box-sizing: border-box;
  	padding: 20px 30px;
  	position: relative;
  	border-radius: 3px;
  	&::before {
  		content: '';
  		display: block;
  		position: absolute;
  		border: 10px solid transparent;
  		border-left-color: $color_bg;
  		border-bottom-color: $color_bg;
  		top: -20px;
  		left: 80px;
  		@media screen and (min-width: 480px) and (max-width: 767px) {
  			left: 60px;
  		}
  	}
  	.field-content {
  		font-size: 16px;
      font-style: italic;
    }
  }
  .more-link {
  	margin-top: 50px;
  	text-align: center;
  	a {
  		@extend %button_big;
  	}
  }
}

