

@mixin katalog_inner_01 {
	.node__content {
		@extend %layout;
		position: relative;
		box-sizing: border-box;
		min-height: 380px;
		padding-left: 540px;
		margin-bottom: 50px;	
		@media screen and (min-width: 768px) and (max-width: 979px) {
			padding-left: 47vw;	
		}	
		@media screen and (min-width: 480px) and (max-width: 767px) {
			padding-left: 10px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding-left: 10px;
		}
	}
	.field--type-image {
		position: absolute;
		top: 0;
		left: 0;
		width: 500px;
		overflow: hidden;
		max-height: 370px;
		border: 1px solid #ddd;
		@media screen and (min-width: 768px) and (max-width: 1179px) { 
			left: 20px; //из-за пэддинга слева не корректно работает %layout, поэтому двигаем влево изображение
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			width: 40vw;
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			position: static;
			width: 460px;
			margin-bottom: 30px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			position: static;
			width: 100%;
			margin-bottom: 30px;
		}
		img {
			width: 100%;
			height: auto;
			display: block;
		}
	}
	.field--name-body {
		margin-bottom: 20px;
		p {
			margin: 0;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			font-size: 14px;
		}
	}
	.field--name-field-cena {
		font-weight: 700;
		font-size: 22px;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			font-size: 18px;
		}
		.field__label {
			display: inline-block;
			margin-right: 10px;
		}
		.field__item {
			display: inline-block;
		}
	}
}
