
/* Вариант 1 */
@mixin wrap_01{

	@media screen and (min-width: 320px) and (max-width: 767px) {
		background-color: #fff;
		padding: 5px 0;
		border-bottom: 2px solid;
		position: fixed;
		width: 100%;
		z-index: 999;
		top: 0;
		.responsive-menu-toggle{
			position: absolute;
		}
	}
}

@mixin not_wrap_01{
	background-color: #fff;
	width: 100%;
	border-bottom: 2px solid #232323;
	box-shadow: 0 1px 2px -2px #000;

	@media screen and (max-width: 767px) {
		width: 100%;
		padding: 5px 0;
		box-shadow: 0 2px 10px 3px rgba(0,0,0,0.5);
		& ~ main {
			margin-top: 35px;
			padding-top: 10px;
		}
	}
	.region-header{
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
		position: relative;
		width: 1140px;
		margin: 0 auto;
		padding: 10px 0 0;
		@media screen and (min-width: 768px) and (max-width: 1179px) {
			width: auto;
		}
		@media screen and (max-width: 767px) {
			width: 100%;
			position: fixed;
			padding: 5px 0;
		}
	}
}

@mixin contact_01{
	width: 100%;
	position: absolute;
	@media screen and (min-width: 480px) and (max-width: 767px) {
		width: auto;
		position: absolute;
		right: 10px;
	}
	@media screen and (min-width: 320px) and (max-width: 479px) {
		display: none;
	}
	.field--name-body{
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			display: block;
			text-align: right;
			padding: 0;
		}
		@extend %layout;
		padding-top: 10px;
	}
	.contact{
		.telephone{
			font-family: Cuprum;
			font-weight: 700;
			margin: 0;
			font-size: 24px;
			@media screen and (min-width: 320px) and (max-width: 767px) {
				font-size: 16px;
				line-height: 1.3;
			}
		}
		.e-mail{
			margin: 0;
			@media screen and (min-width: 320px) and (max-width: 767px) {
				font-size: 12px;
				line-height: 1.3;
			}
			a{
				color: #000;
			}
		}
	}
	.soc-seti{
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			display: none;
		}
		a{
			margin: 0 0 0 5px;
			width: 40px;
			height: 40px;
			text-indent: -9999px;
			background-size: 100% auto;
			background-repeat: no-repeat;
			background-position: 0 0;
			&:hover{
				background-position: 0 bottom;
			}
		}
	}
}

@mixin not_contact_01{
	position: relative;
	order: 2;
	width: 50%;
	float: right;
	@media screen and (min-width: 768px) and (max-width: 1179px) {
		padding: 0 20px 0 0;
	}
	@media screen and (min-width: 768px) and (max-width: 979px) {
		.telephone{
			font-size: 18px;
		}
	}
	@media screen and (max-width: 767px) {
		display: none;
	}
	.field--name-body{
		padding: 0;
		width: auto;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 14px;
		}
	}
	.telephone{
		line-height: 1.1;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 18px;
		}
	}
	.soc-seti{
		a{
			width: 35px;
			height: 35px;
		}
	}
}

@mixin brand_01{
	@extend %layout;
	text-align: center;
	@media screen and (min-width: 480px) and (max-width: 767px) {
		padding: 0 0 0 80px;
		text-align: left;
	}
	@media screen and (min-width: 320px) and (max-width: 479px) {
		padding: 0 0 0 70px;
		text-align: left;
	}
	.site-logo{
		width: 40px;
		display: inline-block;
		border-top: 10px solid #000;
		padding: 20px 30px 10px;
		position: relative;
		z-index: 1;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			border: none;
			padding: 0;
			width: 24px;
			float: left;
			margin-right: 15px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-right: 12px;
		}
	}
	img{
		display: block;	
		width: 100%;
		height: auto;
	}
	.site-name{
		text-transform: uppercase;
		letter-spacing: 2px;
		font-family: Cuprum;
		font-weight: 900;
		font-size: 40px;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			font-size: 16px;
			line-height: 1.3;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			letter-spacing: 1px;
		}
		a{
			text-decoration: none;
			color: #000;
		}
	}
	.site-slogan{
		text-transform: uppercase;
		letter-spacing: 1px;
		font-weight: 700;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			text-transform: inherit;
			letter-spacing: 0;
			font-weight: normal;
			font-size: 12px;
			line-height: 1.3;
		}
	}
}

