		}
	}
}

@mixin menu_01{
	@extend %layout;
	padding: 30px 0;
	@media screen and (max-width: 767px) {
		display: none;
	}
	&.animated-menu{
		top: 0;
		left: 0;
		padding: 10px 0;
		position: fixed;
		border-bottom: 2px solid #000;
		box-shadow: 0 0px 15px 7px rgba(0, 0, 0, 0.3);
		z-index: 999;
		width: 100%;
		background-color: #fff;
		ul{
			justify-content: center;
		}
	}
	ul{
		display: flex;
		flex-flow: row nowrap;
		justify-content: center;
		li{
			float: none;
			text-transform: uppercase;
			letter-spacing: 1px;
			font-weight: 700;
			font-family: Cuprum;
			font-size: 18px;
			border-right: 2px solid #ccc;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				width: 25%;
				text-align: center;
			}
			&:last-child{
				border: none;
			}
			a{
				text-decoration: none;
				padding: 12px 30px;
				color: #000;
				@media screen and (min-width: 768px) and (max-width: 979px) {
					padding: 10px 0;
				}
				&:hover{					
					color: $color_style;
				}
				&.is-active{
					color: $color_style;
				}
			}
			&.menu-item--expanded{
				overflow: hidden;
				&:hover {
					overflow: inherit;
					> ul{
						opacity: 1;
						margin: 0;
						border-top: 4px solid #232323;
					}
				}
				> a::after{
					content: '';
					display: block;
					width: 10px;
					height: 10px;
					position: absolute;
					bottom: 2px;
					left: calc(50% - 5px);
					background-image: url(../images/arrow-bottom.png);
					background-repeat: no-repeat;
					background-size: 100%;
					background-position: center;
				}
			}
		}
		&.sub-nav{
			background-color: #fff;
			box-sizing: border-box;
			flex-flow: column;
			box-shadow: 0 4px 5px -2px rgba(0,0,0,0.3);
			border-top: none;
			transition: 0.2s;
			opacity: 0;
			margin-top: -20px;
			li{
				display: block;
				width: 100%;
				border: none;
				letter-spacing: 0;
				text-transform: inherit;
				border-bottom: 1px solid #ccc;
				a{
					padding: 12px 30px !important;
				}
			}
		}
	}
}

@mixin not_menu_01{
	position: relative;
	top: inherit;
	order: 3;
	width: 100%;
	padding: 30px 0 10px;
	@media screen and (min-width: 768px) and (max-width: 1179px) {
		padding: 10px 20px 0;
	}
	&.animated-menu{
		top: 0;
		left: 0;
		padding: 10px 0;
		position: fixed;
		z-index: 999;
		border-bottom: 2px solid #000;
		box-shadow: 0 0px 15px 7px rgba(0,0,0,0.3);
		ul{
			justify-content: center;
		}
	}
	ul{
		justify-content: left;
		li{
			border: none;
			&:first-child a{
				padding-left: 0;
			}
		}
		a{
			padding: 10px 30px;
			&:hover{					
				color: $color_style;
			}
			&.is-active{
				color: $color_style;
			}
		}
	}
}

@mixin picture_01{
	background-color: $color_style;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	background-attachment: fixed;
	@media screen and (max-width: 1179px) {
		background-attachment: scroll;
	}
	@media screen and (min-width: 320px) and (max-width: 767px) {
		margin-top: 45px;
	}
	.field--type-image{
		display: none;
	}
	.field--name-body{
		@extend %layout;
		padding: 100px 0;
		text-align: center;
		height: 300px;
		color: #fff;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			padding: 50px 0;
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			padding: 50px 0;
			height: 270px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			padding: 30px 0;
			height: auto;
			width: 460px;
			margin: 0 auto;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding: 50px 0;
			height: auto;
			width: 300px;
			margin: 0 auto;
		}
	}
	h1{
		font-family: Cuprum;
		font-size: 80px;
		font-weight: bold;
		letter-spacing: 2px;
		line-height: 80px;
		padding: 0 150px 0px;
		text-align: center;
		text-shadow: 1px 1px 2px #444;
		text-transform: uppercase;
		margin-bottom: 20px;
		margin-top: 20px;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			font-size: 70px;
			line-height: 1.1;
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 60px;
			line-height: 1.1;
			padding: 0 100px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			font-size: 44px;
			line-height: 1.1;
			padding: 0;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			font-size: 30px;
			line-height: 1.3;
			padding: 0;
		}
	}
	p{
		font-size: 18px;
		text-transform: uppercase;
		letter-spacing: 1px;
		font-weight: 700;
		padding: 0 50px;
		text-shadow: 1px 1px 2px #444;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			padding: 0 100px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			padding: 0;
			font-size: 16px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding: 0;
			font-size: 16px;
		}
	}
}
/* конец вариант 1 */


