@import url('https://fonts.googleapis.com/css?family=Cuprum:400,700|PT+Sans:400,400i,700&subset=cyrillic');

@import 'headers';
@import 'cards';
@import 'katalog';
@import 'textblocks';
@import 'news';
@import 'advantages';
@import 'reviews';
@import 'thanks';
@import 'forms';
@import 'footers';
@import 'pages';
@import 'elements';


//Variables
$color_style: #F67800;
$color_style_dark: #c96200;
$color_bg: #fff;
$font_title: Cuprum;
$font_text: PT Sans;
//End of variables


body {
	margin: 0;
	font-size: 16px;
	color: #000;
	line-height: 1.5;
	font-family: $font_text;
	background-color: #232323;
}
#mm-page{
	background-color: #fff;
}
.clearfix::after{
	display: none;
}

%layout {
	width: auto;
	@media screen and (min-width: 1180px) {
		width: 1140px;
		margin: 0 auto;
	}
	@media screen and (min-width: 768px) and (max-width: 1179px) {
		padding-left: 20px;
		padding-right: 20px;
	}
	@media screen and (min-width: 320px) and (max-width: 767px) {
		padding-left: 10px;
		padding-right: 10px;
	}
}


%title_h1{
	font-family: $font_title;
	font-size: 40px;
	line-height: 1.2;
	margin-top: 0;
	margin-bottom: 30px;
	@media screen and (min-width: 768px) and (max-width: 979px) {
		font-size: 36px;
	}
	@media screen and (min-width: 480px) and (max-width: 767px) {
		font-size: 32px;
	}
	@media screen and (min-width: 320px) and (max-width: 479px) {
		font-size: 28px;
	}

}

%title_h2{		
	font-size: 40px;
	text-transform: uppercase;
	font-family: $font_title;
	letter-spacing: 0.06em;
	@media screen and (min-width: 320px) and (max-width: 767px) {
		font-size: 30px;
	}
}
%button_big{
	
}
%button_little{

}
%block {
	padding: 70px 0 100px;
	@media screen and (min-width: 480px) and (max-width: 767px) {
		padding: 50px 0;
	}
}

/* Внутренняя страница */
body:not(.path-frontpage) {
	h1 {
		@extend %layout, %title_h1;
	}
}
/*конец внутренняя страница*/


/* Шапка */
.region-header{ @include wrap_03; }
// body:not(.path-frontpage) header{ @include not_wrap_03 }

#block-nasikontaktyvsapke{ @include contact_03; }
// body:not(.path-frontpage) #block-nasikontaktyvsapke{ @include not_contact_03; }

#block-stark-branding{ @include brand_03; }
// body:not(.path-frontpage) #block-stark-branding{ @include not_brand_03; }

#block-horizontalmenu{ @include menu_03; }
// body:not(.path-frontpage) #block-horizontalmenu{ @include not_menu_03; }

#block-titr{ @include picture_03; }
/* конец шапки */


/* Наши услуги */
#block-views-block-katalog-block-1-2 {
	@include card_style_01;
	@extend %block;
	h2 {
		@extend %title_h2;
		@extend %layout;
		text-align: center;
		margin-bottom: 40px;
	}
}
/* конец наши услуги */

/* Текстовый блок */
#block-ostavitzaavkunaglavnoi{	
	@include textblock_style_01;
	@extend %block;
}
/* конец текстовый блок */

/* Наши преимущества */
#block-nasipreimusestva-2 {
	@include advantage_01;
	@extend %block;
	h2 {
		@extend %title_h2;
		@extend %layout;
		text-align: center;
		margin-bottom: 10px;
	}
	&>.field {
		@extend %layout;
	}
}
/* конец наши преимущества */

/* Каталог */
.path-katalog {
	#block-stark-content {
		@include card_style_02;
	}
}
.page-node-type-tovar-usluga { //Карточка товара
	@include katalog_inner_01;
}
/* конец каталог */

/* Наши партнеры */
#block-our-partners {
	@extend %block;
	@include partners_style_01;
	h2 {
		@extend %layout, %title_h2;
		text-align: center;
		margin: 0 auto 50px;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			margin-bottom: 30px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			margin-bottom: 20px;
		}
	}
	&>.field {
		@extend %layout;
	}
}
/* конец наши партнеры */

/* Благодарности */
#block-views-block-blagodarnosti-block-1 { //благодарности на главной
	@extend %block;
	@include thanks_style_01;
	h2 {
		@extend %layout, %title_h2;
		text-align: center;
		margin: 0 auto 50px;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			margin-bottom: 30px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			margin-bottom: 20px;
		}
	}
	&>.field {
		@extend %layout;
	}
}
.path-blagodarnosti { //раздел Благодарности
	#block-stark-content {
		@include thanks_internal_style_01;
	}
}
/* конец благодарности */


/* Отзывы */
#block-views-block-otzyvy-block-1 { //отзывы на главной
	@extend %block;
	@include reviews_style_01;
	background-color: #ffe2c7;
	h2 {
		@extend %layout, %title_h2;
		text-align: center;
		margin: 0 auto 50px;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			margin-bottom: 30px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			margin-bottom: 20px;
		}
	}
	&>.field {
		@extend %layout;
	}
}
.path-otzyvy { //раздел Отзывы
	#block-stark-content {
		@include reviews_internal_style_01;
	}
}
/* конец отзывы */


/* Новости */
.path-novosti { //Раздел новости
	#block-stark-content {
		@include news_style_01;
	}
}
.page-node-type-novost { //Новость
	#block-stark-content {
		@include news_inner_style_01;
	}
}
/* конец новости */

/* Форма */
#block-form{
	@include form_style_01;
}
/* конец формы */

/* Обычная страница */
.page-node-type-page {
	#block-stark-content {
		@include page_style_01;
	}
}
/* конец обычная страница */


/* Обычная страница */
.path-user {
	#user-login-form {
		@include form_style_01;
	}
	@include user_page_01;	
}
/* конец обычная страница */


/* Футер */
footer{
	@include footer_style_01;
}
/* конец футер */


/* Ссылки */
%normal_link{
	color: #06f;
	&:hover{
		text-decoration: none;
	}
}
/* конец ссылки */

/* Меню */
.responsive-menu-toggle {
	@media screen and (min-width: 768px) {
		display: none;
	}
	.toggle::before{
		background: #000;
		width: 30px;
		height: 4px;
		left: 10px;
		border-radius: 0px;
		top: 8px;
	}
	span.icon{
		background: #000;
		width: 30px;
		height: 4px;
		left: 10px;
		border-radius: 0px;
		top: 16px;
	}
	span.label{
		display: none;
	}
	.toggle::after{
		background: #000;
		width: 30px;
		height: 4px;
		left: 10px;
		border-radius: 0px;
		top: 24px;
	}
}
.mm-opening .responsive-menu-toggle-icon{
	&::before, &::after{ top: 16px !important;}
}
/* конец меню */

/* Социальные сети */
a.fb{ background-image: url(../images/soc01.jpg); }
a.tw{ background-image: url(../images/soc02.jpg); }
a.vk{ background-image: url(../images/soc03.jpg); }
a.in{ background-image: url(../images/soc04.jpg); }
a.ok{ background-image: url(../images/soc05.jpg); }
a.yt{ background-image: url(../images/soc06.jpg); }
/* конец социальные сети */

/*Breadcrumbs*/
#block-stark-breadcrumbs {
	@include breadcrumbs_01;
}
/*end of breadcrumbs*/

/* Кнопка */
%button_big {
	display: inline-block;
	text-decoration: none;
	color: #fff;
	font-size: 20px;
	font-weight: 300;
	line-height: 1;
	padding: 15px 50px;
	background-color: $color_style;
	box-shadow: 0px 2px 3px -1px #444;
	font-family: Cuprum;
	text-transform: uppercase;
	letter-spacing: 0.06em;
	border-bottom: 4px solid darken($color_style, 10%);
	border-left: none;
	border-right: none;
	border-top: none;
	border-radius: 5px;
	&:hover, &:focus {
		background-color: darken($color_style, 5%);
	}
	@media screen and (min-width: 768px) and (max-width: 979px) {
		font-size: 18px;
	}
	@media screen and (min-width: 480px) and (max-width: 767px) {
		font-size: 16px;
	}
	@media screen and (min-width: 320px) and (max-width: 479px) {
		font-size: 14px;
		padding: 10px 30px;
	}
}
/* конец кнопка */

/* Контекстное меню */
.contextual-region .contextual {
	&.open{
		ul.contextual-links{
			display: block !important;
		}
	}
	ul.contextual-links{
		display: none !important;
		li a{
			color: #333 !important;
			padding: 0.4em 0.6em !important;
		}
	}
}
/* конец контекстное меню */