/* Вариант 1 */
@mixin footer_style_01{
	background-color: #232323;
	.region-footer{
		@extend %layout;
		padding: 50px 0;
		color: #fff;
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
		@media screen and (min-width: 768px) and (max-width: 1179px)  {
			padding: 50px 20px;
		}
		@media screen and (min-width: 480px) and (max-width: 767px)  {
			padding: 30px 10px;
			font-size: 12px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px)  {
			padding: 50px 10px 20px;
		}
		.block{
			h2 {
				font-family: Cuprum;
				font-size: 20px;
				text-transform: uppercase;
				letter-spacing: 0.06em;
				line-height: 1.1;
				margin: 0 0 20px;
			}
			p{
				margin: 0 0 10px;
				&.telefon{
					font-size: 30px;
					line-height: 1.2;
					font-family: Cuprum;
					letter-spacing: 0.06em;
					@media screen and (min-width: 768px) and (max-width: 1179px)  {
						font-size: 24px;
					}
					@media screen and (min-width: 480px) and (max-width: 767px)  {
						font-size: 24px;
					}
					@media screen and (min-width: 320px) and (max-width: 479px)  {
						font-size: 24px;
					}	
				}
			}
			.seti{
				display: flex;
				flex-flow: row nowrap;
				justify-content: start;
				a{
					margin: 0 5px 0 0;
					width: 40px;
					height: 40px;
					text-indent: -9999px;
					background-size: 100% auto;
					background-repeat: no-repeat;
					background-position: 0 0;
					@media screen and (min-width: 320px) and (max-width: 767px)  {
						width: 30px;
						height: 30px;
					}
					&:hover{
						background-position: 0 bottom;
					}
				}
			}
			a{
				color: $color_style;
				&:hover{
					text-decoration: none;
				}
			}
			&:nth-child(1){
				width: 50%;
				box-sizing: border-box;
				padding: 0 200px 0 0;
				@media screen and (min-width: 768px) and (max-width: 1179px)  {
					padding: 0 10% 0 0;
				}
				@media screen and (min-width: 480px) and (max-width: 767px)  {
					padding: 0 5% 0 0;
				}
				@media screen and (min-width: 320px) and (max-width: 479px)  {
					width: 100%;
					padding: 0;
					margin: 0 0 40px;
				}	
			}
			&:nth-child(2){
				width: 50%;
				text-align: right;
				@media screen and (min-width: 320px) and (max-width: 479px)  {
					width: 100%;
					text-align: left;
				}	
			}
			&:nth-child(3){
				width: 100%;
				padding: 50px 0 0;
				table{
					width: 100%;
					font-size: 14px;
					td{
						&:nth-child(2){
							text-align: right;
						}
					}
				}
			}
		}
	}
}
/* конец вариант 1 */