$color_style: #F67800;

%ucase {
	text-transform: uppercase;
	letter-spacing: 0.06em;
}


@mixin card_style_01 {
	.view { 
		@extend %layout;
	}
	.view-content {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
	}
	.views-field {
		text-align: center;
		margin-bottom: 20px;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			text-align: left;
			margin-bottom: 10px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 10px;
		}

	}
	.views-row {
		width: 33.333%;
		box-sizing: border-box;
		padding: 0 20px;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			padding: 0 0 0 190px;
			width: 100%;
			position: relative;
			margin-bottom: 30px;
			min-height: 170px;
			display: flex;
			flex-flow: column nowrap;
			justify-content: center;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			width: 100%;
			padding: 0;
			margin-bottom: 50px;
		}
		
	}
	.views-field-field-image {
		margin-bottom: 30px;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			position: absolute;
			top: 0;
			left: 0;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 15px;
		}
		.field-content {
			overflow: hidden;
			margin: 0 auto;
			width: 250px;
			height: 250px;
			border-radius: 50%;
			box-shadow: 3px 3px 5px 0 #444;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				width: 25vw;
				height: 25vw;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				width: 160px;
				height: 160px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 170px;
				height: 170px;
			}
			img {
				width: auto;
				height: 100%;
			}
		}
	}
	.views-field-title {
		line-height: 1;
		.field-content {
			a {
				color: inherit;
				text-decoration: none;
				border-bottom: 1px solid;
				font-weight: 700;
				font-family: $font_title;
				font-size: 28px;
				&:hover, &:focus {
					border-color: transparent;
				}
				@media screen and (min-width: 768px) and (max-width: 979px) {
					font-size: 20px;
				}
				@media screen and (min-width: 480px) and (max-width: 767px) {
					font-size: 18px;				
				}
			}
		}
	}
	.views-field-body {
		.field-content {
			@media screen and (max-width: 979px) {
				font-size: 14px;
			}
		}
	}
	.views-field-field-cena {
		.field-content {
			font-size: 24px;
			font-weight: 700;
			font-family: $font_title;
			@media screen and (max-width: 979px) {
				font-size: 20px;
			}
		}
	}
	.more-link {
		text-align: center;
		margin-top: 30px;
		a {
			@extend %button_big;
		}
	}
}

@mixin card_style_02 {
