	}
	.views-field-title {
		line-height: 1;
		.field-content {
			a {
				color: inherit;
				text-decoration: none;
				border-bottom: 1px solid;
				font-weight: 700;
				font-family: $font_title;
				font-size: 28px;		
				&:hover, &:focus {
					border-color: transparent;
				}		
				@media screen and (min-width: 980px) and (max-width: 1179px) {
					font-size: 24px;
				}
				@media screen and (min-width: 768px) and (max-width: 979px) {
					font-size: 20px;
				}
				@media screen and (min-width: 480px) and (max-width: 767px) {
					font-size: 20px;				
				}
			}
		}
	}
	.views-field-body {
		.field-content {
			@media screen and (max-width: 979px) {
				font-size: 14px;
			}
		}
	}
	.views-field-field-cena {
		margin-bottom: 40px;
		.field-content {
			font-size: 24px;
			font-weight: 700;
			font-family: $font_title;
			@media screen and (max-width: 979px) {
				font-size: 20px;
			}
		}
	}
	.more-link {
		text-align: center;
		margin-top: 30px;
		a {
			@extend %button_big;
		}
	}
}

