

@mixin advantage_01 {	
	background-color: #ffc58f;
	@media screen and (min-width: 320px) and (max-width: 767px) {
		padding: 50px 0;
	}
	h2{
		margin: 0 auto;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			margin: 0 0 10px;
			font-size: 30px;
			line-height: 1.1;
		}
	}
	.field--name-body {
		p {
			text-align: center;
			font-size: 20px;
			margin: 0 auto 50px;
			@media screen and (min-width: 768px) {
				width: 650px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 	16px;
				margin: 0 0 30px;
			}
		}
	}
	.field--type-field-collection {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;		
		&>.field__item {
			width: 25%;
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 100%;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				margin-bottom: 30px;	
			}
		}				
	}
	.content {
		box-sizing: border-box;
		padding: 0 20px;
		text-align: center;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			padding: 0 10px;	
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			padding: 0 5px;	
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding: 0;
			text-align: left;	
		}
	}
	.field--name-field-piktogramma {
		width: 80px;
		height: 80px;
		margin: 0 auto 20px;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			width: 70px;
			height: 70px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			float: left;
			margin-right: 20px;	
			margin-bottom: 0;
			width: 70px;
			height: 70px;
		}
		img {
			width: 100%;
			height: auto;
			display: block;
		}
	}
	.field--name-field-zagolovok {
		margin-bottom: 5px;
		font-size: 28px;
		font-weight: 700;
		line-height: 1.1;
		font-family: $font-title;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			font-size: 14px;
			line-height: 1.2;

		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			margin-bottom: 10px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 0;
			font-size: 24px;
		}
	}
	.field--name-field-opisanie {
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 14px;
		}	
		@media screen and (min-width: 320px) and (max-width: 767px) {
			font-size: 12px;	
		}
	}

}