/*Breadcrumbs*/
@mixin breadcrumbs_01 {
	.breadcrumb {
		@extend %layout;
		margin-top: 20px;
		ol {
			margin: 0;
			padding: 0;
			list-style-type: none;
		}
		li {
			display: inline-block;
			margin-right: 5px;
			color: #999;
		}
		li:not(:first-child) {
			&::before {
				content: '»';
				display: inline-block;
				margin-right: 5px;
			}
		}
		a {
			color: inherit;
			font-size: 16px;
			&:hover, &:focus {
				color: #000;
				text-decoration: none;
			}
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 14px;
			}
			@media screen and (max-width: 767px) {
				font-size: 12px;
			}
		}
	}
}
/*end of breadcrumbs*/

/*Partners*/
@mixin partners_style_01 {
	.field--type-field-collection {
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
		&>.field__item {
			box-sizing: border-box;
			padding: 0 20px;
			width: 20%;
			margin-bottom: 40px;
			@media screen and (min-width: 320px) and (max-width: 767px) {
				padding: 0 10px;
				width: 33.333%;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				margin-bottom: 20px;
			}
		}
	}	
	.field--name-field-piktogramma {
		height: 170px;
		margin-bottom: 20px;
		text-align: center;
		@media screen and (min-width: 320px) and (max-width: 1179px) {
			height: 15vw;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 5px;
		}
		&::before {
			content: '';
			display: inline-block;
			height: 100%;
			vertical-align: middle;
		}
		img {
			display: inline-bblock;
			width: auto;
			height: auto;
			max-width: 90%;
			max-height: 90%;
			vertical-align: middle;			
		}
	}
	.field--name-field-zagolovok {
		text-align: center;
		font-size: 20px;
		@media screen and (min-width: 768px) and (max-width: 1179px) {
			font-size: 16px;
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			font-size: 14px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			font-size: 12px;
		}
	}
}
/*end of partners*/
