
/* Вариант 1 */
@mixin textblock_style_01{
	background-color: #efefef;
	padding: 100px 0;
	@media screen and (min-width: 768px) and (max-width: 1179px) {
		padding: 70px 0;
	}
	@media screen and (min-width: 320px) and (max-width: 767px) {
		padding: 50px 0;
	}
	.field--name-body{
		@extend %layout;
	}
	table{
		width: 100%;
		td{
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 100% !important;
				display: block;
				text-align: center !important;
			}
			&:nth-child(1){
				text-align: right;
				width: 60%;
				@media screen and (min-width: 480px) and (max-width: 979px) {
					width: 50%;
				}
			}
			&:nth-child(2){
				text-align: center;
				width: 40%;
				@media screen and (min-width: 480px) and (max-width: 979px) {
					width: 50%;
				}
			}
		}
		h2{
			@extend %title_h2;
			margin: 0 0 20px;
			font-size: 60px;
			line-height: 1.1;
			@media screen and (min-width: 980px) and (max-width: 1179px) {
				font-size: 50px;
			}
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 40px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 24px;
				margin: 0 0 10px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 30px;
				margin: 0 0 10px;
				line-height: 1.3;
			}
		}
		p{
			font-size: 24px;
			line-height: 1.3;
			@media screen and (min-width: 480px) and (max-width: 979px) {
				font-size: 18px;
				margin: 0;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 18px;
				margin: 0 0 20px;
			}
		}
		a{
			@extend %button_big;
			@media screen and (min-width: 320px) and (max-width: 767px) {
				padding: 12px 25px !important;
				font-size: 16px !important;
			}
		}
	}
}
/* конец вариант 1 */